 .float-btn {
  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: 99;
  text-align: right;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  padding: 7px;
  box-shadow: 2px 2px 4px #555;
  border: 2px solid #fff;
}

 .float-btn::after {
  content: '';
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  display: block;
  background: #fff;
  position: absolute;
  top: -4px;
  left: -4px;
  border-radius: 50%;
  z-index: -1;
}

.float-btn::before {
  content: '';
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background: #fff;
  bottom: -1px;
  left: 0px;
  transform: skew(-10deg, -10deg);
  z-index: -1;
  box-shadow: 2px 2px 4px #555;
}
.float-btn span::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  background: linear-gradient(35deg, rgb(42, 183, 64) 45%, #54EC68 100%);
  border-radius: 50%;
  top: 0;
  left: 0;
  z-index: 0;
}
.float-btn span::before {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  display: block;
  background: #2AB740;
  bottom: 3px;
  left: 4px;
  transform: skew(-10deg, -10deg);
  z-index: 1;
}

.float-btn a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  border-radius: 50%;
}

.float-btn .fa-whatsapp { position: relative; top: 2px; left: 1px; z-index: 2; }
.float-btn .fa-whatsapp::before { content: '\f095'; font-size: 32px; color: #fff; }

